import {Button, Container} from "@mui/material";
import Toolbar from "@mui/material/Toolbar";
import {Box} from "@mui/system";
import React from "react";
import {useNavigate} from "react-router-dom";
import {useEffect, useState} from 'react';
import {DataGrid} from '@mui/x-data-grid';
import AddCircleOutlineOutlinedIcon from '@mui/icons-material/AddCircleOutlineOutlined';
import {esES} from '@mui/x-data-grid'
import TextField from "@mui/material/TextField";
import Autocomplete from "@mui/material/Autocomplete";
import Grow from '@mui/material/Grow'

export default function Carreras(){
    const [facultades,setFacultades] = useState(null);
    const [carreras,setCarreras] = useState([])
    const [fac,setFac] = useState(null);
    const [showCarreras,setShowCarreras] = useState(false);

    /*Loaders*/
    async function loadFacultades(){
        const response = await fetch('http://localhost:4000/facultades')
        const data = await response.json()
        var newList =[]
        for( var i=0; i<data.length;i++){
            var obj = {label: data[i].FNombre, id: data[i].id}
            newList.push(obj)
        }
        setFacultades(newList)
    }


    /*Handlers*/
    const handleChangeFF = (e,v) => {
        if( v === null ){
            setFac(null)
            setShowCarreras(false)
        }
        else{
            setFac(v)
            setShowCarreras(true)
            loadCarreras(v.id)
        }
    };


    const navigate = useNavigate()
    const loadCarreras = async (idFac) => {
        const response = await fetch('http://localhost:4000/carreras/' + idFac)
        const data = await response.json()
        for( var i=0;i<data.length;i++){
            if(data[i].CTipo === "G") {
                data[i].CTipo = "Grado";
            }
            else if (data[i].CTipo === "P"){
                data[i].CTipo = "Posgrado";
            }
        }
        setCarreras(data)
    }

    const columnas = [
        {field: 'id', headerName: 'Codigo', flex: 1, hide: true},
        {field: 'CNombre', headerName: 'Nombre',flex: 1},
        {field: 'CTipo', headerName: 'Tipo', flex: 1},
        {field: 'CObservacion', headerName: 'Observacion', flex: 1},
        {field: 'CDepartamento', headerName: 'Departamento', flex: 1},
    ]


    useEffect(() => {
        loadFacultades()
    }, [])


    return(
        <Box sx={{flexGrow: 1, flex: 0, padding: 0}}>
            <Container>
                <>
                    <h1> Carreras </h1>
                    <Box>
                        <Autocomplete
                            disablePortal
                            id="idF"
                            name="idF"
                            options={facultades}
                            value={fac}
                            sx={{width:450, p: 1}}
                            onChange={handleChangeFF}
                            fullWidth
                            //onChange={handleChangeFF}
                            //onMouseLeave={mouseLeaveFF}
                            renderInput={(params) =>
                                <TextField
                                    {...params}
                                    label="Facultad"/>}
                        />
                    </Box>
                    <Box>
                        <Grow in={showCarreras} unmountOnExit>
                            <div style={{height: '100%' }}>
                                <DataGrid
                                    style={{backgroundColor: '#8ab8ac', color: 'black'}}
                                    localeText={esES.components.MuiDataGrid.defaultProps.localeText}
                                    autoHeight
                                    rows={carreras}
                                    columns={columnas}
                                    pageSize={5}
                                    rowsPerPageOptions={[5]}
                                    minHeight={750}
                                />
                            </div>
                        </Grow>
                    </Box>
                </>
                <Toolbar>
                    <Button
                        variant='contained'
                        style={{marginRight: '.5rem',marginLeft: '.5rem', backgroundColor: 'green', color: 'white'}}
                        onClick={() => navigate('/carreras/new')}
                        startIcon={<AddCircleOutlineOutlinedIcon />}
                    >
                        Agregar
                    </Button>
                </Toolbar>
            </Container>
        </Box>
    )
}