import {Button, Container} from "@mui/material";
import Toolbar from "@mui/material/Toolbar";
import {Box} from "@mui/system";
import React from "react";
import {useNavigate} from "react-router-dom";
import {useEffect, useState} from 'react';
import {DataGrid} from '@mui/x-data-grid';
import AddCircleOutlineOutlinedIcon from '@mui/icons-material/AddCircleOutlineOutlined';
import {esES} from '@mui/x-data-grid'
import {useLocation} from "react-router";
import Grid from "@mui/material/Grid";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';


export default function InstanciaList(){
    const {state} = useLocation()
    const {ida,idf,idc} = state;

    const [instancias,setInstancias] = useState([]);

    /*Loaders*/
    async function loadInstancias(idA){
        const response = await fetch('http://localhost:4000/instancias/' + idA)
        const data = await response.json()
        const response2 = await fetch('http://localhost:4000/loadTiposInstancia')
        const data2 = await response2.json()

        for(var i=0;i<data.length;i++){
            for(var j=0;j<data2.length;j++){
                if(data[i].idTI === data2[j].id){
                    data[i].idTI = data2[j].TITipo;
                }
            }
        }
        setInstancias(data)
    }


    const navigate = useNavigate()

    const columnas = [
        {field: 'id', headerName: 'Codigo', flex: 1, hide: true},
        {field: 'idTI', headerName: 'Tipo', flex: 1},
        {field: 'IObservacion', headerName: 'Observación', flex: 1},
        {field: 'IFechaInicio', headerName: 'Fecha de Inicio', flex: 1},
        {field: 'IFechaPresentacion', headerName: 'Fecha de Presentación', flex: 1},
        {field: 'IFechaLimite', headerName: 'Fecha Limite', flex: 1},
        {field: 'document', headerName: 'Documentacion', flex: 1, renderCell: (cellValues) => {return (
                <Button
                    color="inherit"
                >Documentacion</Button>)}}
    ]


    useEffect(() => {
        loadInstancias(ida)
    }, [])


    return(
        <Box sx={{flexGrow: 1, flex: 0, padding: 0}}>
            <Container>
                <>
                    <h1> Instancias </h1>
                    <Box>
                        <div style={{height: '100%' }}>
                            <DataGrid
                                style={{backgroundColor: '#8ab8ac', color: 'black'}}
                                localeText={esES.components.MuiDataGrid.defaultProps.localeText}
                                autoHeight
                                rows={instancias}
                                columns={columnas}
                                pageSize={5}
                                rowsPerPageOptions={[5]}
                                minHeight={750}
                            />
                        </div>
                    </Box>
                </>
                <Toolbar>
                    <Grid container spacing={3}>
                        <Grid item xs={12} sm={6} display="flex" justifyContent="flex-start" >
                            <Button
                                variant='contained'
                                style={{marginRight: '.5rem',marginLeft: '.5rem', backgroundColor: '#8ab8ac', color: 'black'}}
                                onClick={() => {navigate('/acreditaciones' , {state: {idf: idf, idc: idc}})}}
                                startIcon={<ArrowBackIcon />}
                            >
                                Volver
                            </Button>
                        </Grid>
                        <Grid item xs={12} sm={6} display="flex" justifyContent="flex-end" >
                            <Button
                            variant='contained'
                            style={{marginRight: '.5rem',marginLeft: '.5rem', backgroundColor: 'green', color: 'white'}}
                            onClick={() => {navigate('/instancias/new' , {state: {ida: ida, idf: idf, idc: idc}})}}
                            startIcon={<AddCircleOutlineOutlinedIcon />}
                            >
                                Agregar
                            </Button>
                        </Grid>
                    </Grid>

                </Toolbar>
            </Container>
        </Box>
    )
}