import Grid from "@mui/material/Grid";
import TextField from "@mui/material/TextField";
import {DesktopDatePicker, esES, LocalizationProvider} from "@mui/x-date-pickers";
import {AdapterDayjs} from "@mui/x-date-pickers/AdapterDayjs";
import esLocale from "date-fns/locale/es";
import * as React from "react";
import {createTheme, styled, ThemeProvider} from "@mui/material/styles";
import Container from "@mui/material/Container";
import Paper from "@mui/material/Paper";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import Dialog from "@mui/material/Dialog";
import DialogTitle from "@mui/material/DialogTitle";
import IconButton from "@mui/material/IconButton";
import CloseIcon from "@mui/icons-material/Close";
import * as PropTypes from "prop-types";
import {useNavigate} from "react-router-dom";
import {useEffect, useState} from "react";
import dayjs from "dayjs";
import Autocomplete from "@mui/material/Autocomplete";
import {useLocation} from "react-router";
import Button from "@mui/material/Button";
import DialogContent from "@mui/material/DialogContent";
import DialogActions from "@mui/material/DialogActions";

const theme = createTheme({
    palette: {
        Switches: {
            main: '#15A97D'
        },
        rmv: {
            main: '#ff0000',
        },
        add: {
            main: '#008000',
        }
    }
});


/*PopUp*/
const BootstrapDialog = styled(Dialog)(({ theme }) => ({
    '& .MuiDialogContent-root': {
        padding: theme.spacing(2),
    },
    '& .MuiDialogActions-root': {
        padding: theme.spacing(1),
    },
}));
function BootstrapDialogTitle(props) {
    const { children, onClose, ...other } = props;

    return (
        <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
            {children}
            {onClose ? (
                <IconButton
                    aria-label="close"
                    onClick={onClose}
                    sx={{
                        position: 'absolute',
                        right: 8,
                        top: 8,
                        color: (theme) => theme.palette.grey[500],
                    }}
                >
                    <CloseIcon />
                </IconButton>
            ) : null}
        </DialogTitle>
    );
}
BootstrapDialogTitle.propTypes = {
    children: PropTypes.node,
    onClose: PropTypes.func.isRequired,
};



export default function InstanciaAlta() {
    const {state} = useLocation()
    const {ida,idf,idc} = state;
    const navigate = useNavigate()



    /*Datos*/

    const [tI,setTI] = useState(null);

    const [tiposInstancia,setTiposInstancia] = useState([])
    /*const tiposInstancia = [
        {label: 'Formalización', id: 1},
        {label: 'Presentación del FE', id: 2},
        {label: 'Vistas', id: 3},
        {label: 'Respuesta a la vista', id: 4},
        {label: 'Dictamen', id: 5},
        {label: 'Recusación Dictamen', id: 6},
        {label: 'Pares de Evaluadores', id: 7},
        {label: 'Recusación P. Evaluador', id: 8},
        {label: 'UNSL-coneau', id: 9},
        {label: 'Coneau-UNSL', id: 10}
    ]*/


    const [dateFIIns,setDateFIIns] = React.useState(null);
    const [dateFPIns,setDateFPIns] = React.useState(null);
    const [dateFLIns,setDateFLIns] = React.useState(null);
    const [instancia, setInstancia] = useState({
        observacion: "",
        fechaInicio: "",
        fechaPresentacion: "",
        fechaLimite: "",
        tipo: 1,
        estado: "",
        documentos: false,
    })
    const [popUp,setPopUp] = React.useState(false);
    const [error,setError] = React.useState(false);



    /*Loaders*/
    async function loadTiposInstancia(){
        const response = await fetch('http://localhost:4000/loadTiposInstancia')
        const data = await response.json()
        var newList =[]
        for( var i=0; i<data.length;i++){
            var obj = {label: data[i].TITipo, id: data[i].id}
            newList.push(obj)
        }
        setTiposInstancia(newList)
    }


    /*Handlers*/
    const handleChangeTI = (e,v) => {
        if( v === null ){
            setTI(null)
            setInstancia({...instancia, 'idTI': null});
        }
        else{
            setTI(v)
            setInstancia({...instancia, 'idTI': v.id});
        }
    };

    const handleChangeIns = e =>{
        setInstancia({
            ...instancia, [e.target.name]: e.target.value
        });
    }
    const handleChangeFIIns = e => {
        if(e === null){
            setDateFIIns(null)
            setInstancia({...instancia, 'fechaInicio': ""});
        }
        else{
            setDateFIIns(e);
            setInstancia({...instancia, 'fechaInicio': (e).format('DD/MM/YYYY')});
        }
    };
    const handleChangeFPIns = e => {
        if (e === null) {
            setDateFPIns(null)
            setInstancia({...instancia, 'fechaPresentacion': ""});
        } else {
            setDateFPIns(e);
            setInstancia({...instancia, 'fechaPresentacion': (e).format('DD/MM/YYYY')});
        }
    };
    const handleChangeFLIns = e => {
        if (e === null) {
            setDateFLIns(null)
            setInstancia({...instancia, 'fechaLimite': ""});
        } else {
            setDateFLIns(e);
            setInstancia({...instancia, 'fechaLimite': (e).format('DD/MM/YYYY')});
        }
    };
    const handleAlta = async () =>{
        /**
         * Alta Instasncia
         */
        var acrAlt = {
            IObservacion: instancia.observacion,
            IFechaInicio: instancia.fechaInicio,
            IFechaPresentacion: instancia.fechaPresentacion,
            IFechaLimite: instancia.fechaLimite,
            idA: ida,
            idTI: tI.id,
        }
        const res = await fetch('http://localhost:4000/instancias', {
            method: 'POST',
            body: JSON.stringify(acrAlt),
            headers: {'Content-Type': 'application/json'},
        })
        const data = await res.json();
        /*Recupero ID de la acreditacion para linkear Personas e Instancias*/
        var idA = data.id;
        if(idA === null || idA === undefined){
            setError(true);
            handleClickOpen();
        }
        handleClickOpen();
    }



    /*PopUp*/
    function handleClickOpen(){
        setPopUp(true)
    }
    const handleClose = () => {
        setPopUp(false)
    }
    const handleClosePopUp = () => {
        navigate('/instancias/list', {state: {ida: ida, idf: idf, idc: idc}});
    }




    /*Start Loader*/
    useEffect(() => {
        loadTiposInstancia()
    }, [])

    return(
        <ThemeProvider theme={theme}>
            <Container component="main" maxWidth="md" sx={{ mb: 4 }} style={{backgroundColor: 'lightblue'}}>
                <Paper variant="outlined" sx={{ my: { xs: 3, md: 6 }, p: { xs: 2, md: 3 } }} style={{backgroundColor: '#7dcfb6'}}>
                    <Typography variant="h6" gutterBottom align="center">
                        Agregar Instancia
                    </Typography>
                    <React.Fragment>
                        <Box sx={{flexGrow: 1, flex: 0, padding: 0}}>
                            <Container>
                                <>
                                    <Box>
                                        <Grid container spacing={3}>
                                            <Grid item xs={12}>
                                                <Autocomplete
                                                    required
                                                    disablePortal
                                                    id="idTI"
                                                    name="idTI"
                                                    options={tiposInstancia}
                                                    value={tI}
                                                    sx={{width:500, p: 1}}
                                                    onChange={handleChangeTI}
                                                    fullWidth
                                                    renderInput={(params) =>
                                                        <TextField
                                                            required
                                                            {...params}
                                                            label="Facultad"/>}
                                                />
                                            </Grid>
                                            <Grid item xs={12}>
                                                <TextField
                                                    value={instancia.observacion}
                                                    variant='filled'
                                                    id='observacion'
                                                    name='observacion'
                                                    label='Observación'
                                                    multiline
                                                    rows={3}
                                                    fullWidth
                                                    onChange={handleChangeIns}
                                                    inputProps={{style: {color: 'black'}}}
                                                    InputLabelProps={{style: {color: 'black'}}}
                                                />
                                            </Grid>
                                            <Grid item xs={12} sm={4}>
                                                <LocalizationProvider
                                                    dateAdapter={AdapterDayjs}
                                                    adapterLocale={esLocale}
                                                    localeText={esES.components.MuiLocalizationProvider.defaultProps.localeText}
                                                >
                                                    <DesktopDatePicker
                                                        disableFuture
                                                        variant='filled'
                                                        id='fechaInicioIns'
                                                        name='fechaInicioIns'
                                                        label="Fecha de Inicio"
                                                        inputFormat="DD/MM/YYYY"
                                                        value={dateFIIns}
                                                        sx={{width:300, p: 1, display: 'block'}}
                                                        onChange={handleChangeFIIns}
                                                        inputProps={{style: {color: 'black'}}}
                                                        InputLabelProps={{style: {color: 'black'}}}
                                                        renderInput={(params) => <TextField
                                                            required
                                                            {...params}
                                                            sx={{width:200, p: 1}}
                                                        />}
                                                    />
                                                </LocalizationProvider>
                                            </Grid>
                                            <Grid item xs={12} sm={4}>
                                                <LocalizationProvider dateAdapter={AdapterDayjs}>
                                                    <DesktopDatePicker
                                                        variant='filled'
                                                        id='fechaPresentacionIns'
                                                        name='fechaPresentacionIns'
                                                        label="Fecha de Presentación"
                                                        inputFormat="DD/MM/YYYY"
                                                        value={dateFPIns}
                                                        sx={{width:200, m: 1, display: 'block'}}
                                                        onChange={handleChangeFPIns}
                                                        inputProps={{style: {color: 'black'}}}
                                                        InputLabelProps={{style: {color: 'black'}}}
                                                        renderInput={(params) => <TextField
                                                            {...params}
                                                            sx={{width:200, p: 1}}/>}
                                                    />
                                                </LocalizationProvider>
                                            </Grid>
                                            <Grid item xs={12} sm={4}>
                                                <LocalizationProvider dateAdapter={AdapterDayjs}>

                                                    <DesktopDatePicker
                                                        variant='filled'
                                                        id='fechaLimiteIns'
                                                        name='fechaLimiteIns'
                                                        label="Fecha Límite"
                                                        inputFormat="DD/MM/YYYY"
                                                        value={dateFLIns}
                                                        sx={{width:200, m: 1, display: 'block'}}
                                                        onChange={handleChangeFLIns}
                                                        inputProps={{style: {color: 'black'}}}
                                                        InputLabelProps={{style: {color: 'black'}}}
                                                        renderInput={(params) => <TextField
                                                            {...params}
                                                            sx={{width:200, p: 1}}/>}
                                                    />

                                                </LocalizationProvider>
                                            </Grid>
                                        </Grid>
                                    </Box>
                                    <Box>
                                        <Grid container spacing={3} >
                                            <Grid item xs={12}></Grid>
                                            <Grid item xs={12} display="flex" justifyContent="flex-end" >
                                                <Button
                                                    variant='contained'
                                                    style={{marginRight: '.5rem',marginLeft: '.5rem', backgroundColor: 'green', color: 'white'}}
                                                    onClick={handleAlta}
                                                >
                                                    Agregar
                                                </Button>
                                            </Grid>
                                        </Grid>
                                    </Box>
                                </>
                            </Container>
                        </Box>
                    </React.Fragment>
                </Paper>
            </Container>
            <div>
                {(error) ? (
                    <BootstrapDialog
                        onClose={handleClose}
                        aria-labelledby="customized-dialog-title"
                        open={popUp}
                    >
                        <BootstrapDialogTitle id="customized-dialog-title" onClose={handleClose}>
                            Error
                        </BootstrapDialogTitle>
                        <DialogContent>
                            No se pudo agregar la Instancia
                        </DialogContent>
                        <DialogActions>
                            <Button autoFocus onClick={handleClose}>
                                Volver
                            </Button>
                        </DialogActions>
                    </BootstrapDialog>
                ) : (
                    <BootstrapDialog
                        onClose={handleClose}
                        aria-labelledby="customized-dialog-title"
                        open={popUp}
                    >
                        <BootstrapDialogTitle id="customized-dialog-title" onClose={handleClose}>
                            Éxito
                        </BootstrapDialogTitle>
                        <DialogContent>
                            Se agregó correctamente la Instancia
                        </DialogContent>
                        <DialogActions>
                            <Button autoFocus onClick={handleClosePopUp}>
                                Aceptar
                            </Button>
                        </DialogActions>
                    </BootstrapDialog>
                )}
            </div>
        </ThemeProvider>
);
}