import {Button, Container} from "@mui/material";
import Toolbar from "@mui/material/Toolbar";
import {Box} from "@mui/system";
import React from "react";
import {useNavigate} from "react-router-dom";
import {useEffect, useState} from 'react';
import {DataGrid} from '@mui/x-data-grid';
import AddCircleOutlineOutlinedIcon from '@mui/icons-material/AddCircleOutlineOutlined';
import {esES} from '@mui/x-data-grid'
import DeleteIcon from "@mui/icons-material/Delete";
import HistoryEduIcon from "@mui/icons-material/HistoryEdu";



export default function PersonasList(){

    const navigate = useNavigate()

    const [personas, setPersonas] = useState([])
    const [peSel,setPeSel] = useState(null);
    const handleRowClick = (e,v) => {
        setPeSel(e.row)
    }

    const loadPersonas = async () => {
        const response = await fetch('http://localhost:4000/personas')
        const data = await response.json()
        for( var i=0;i<data.length;i++){
            if(data[i].PTipoDocumento === 1) {
                data[i].TipoDocumento = "DNI";
            }
            else if(data[i].PTipoDocumento === 2){
                data[i].TipoDocumento = "LE";
            }
            else if(data[i].PTipoDocumento === 3){
                data[i].TipoDocumento = "LC";
            }
            else{
                data[i].TipoDocumento = "CI";
            }
            if(data[i].PSexo === 1){
                data[i].Sexo = "Masculino";
            }
            else{
                data[i].Sexo = "Femenino";
            }
        }
        setPersonas(data)
    }

    function timeout(delay){
        return new Promise (res => setTimeout(res,delay))
    }

    const handleClick = async (e, v) => {
        await timeout(1000)
        console.log("hacer algo con la rowSel")
        console.log(peSel)
        console.log(peSel.id)
    }

    const columnas = [
        {field: 'delete', headerName: 'Borrar', flex:1, maxWidth: 55,
            renderCell: (cellValues) => {return (
                <Button
                    onClick={() => {navigate('/personas/delete' , {state: {idp: cellValues.id}})}}
                    color="inherit"
                ><DeleteIcon /></Button>)}},
        {field: 'edit', headerName: 'Editar', flex:1, maxWidth: 70,
            renderCell: (cellValues) => {return (
                <Button
                    onClick={() => {navigate('/personas/edit' , {state: {idp: cellValues.id}})}}
                    color="inherit"
                ><HistoryEduIcon /></Button>)}},
        {field: 'id', headerName: 'Codigo', hide: true, flex: 1},
        {field: 'PNombre', headerName: 'Nombre', flex: 1},
        {field: 'PApellido', headerName: 'Apellido', flex: 1},
        {field: 'PTipoDocumento', headerName: 'PTipo de Documento', hide: true, flex: 1},
        {field: 'TipoDocumento', headerName: 'Tipo de Documento', flex: 1},
        {field: 'PNumeroDocumento', headerName: 'Numero de Documento', flex: 1},
        {field: 'PSexo', headerName: 'PSexo', hide: true, flex: 1},
        {field: 'Sexo', headerName: 'Sexo', flex: 1},
        {field: 'PMail', headerName: 'Mail', minWidth: 200, flex: 1},
        {field: 'PNumeroTelefono', headerName: 'Telefono', flex: 1},
    ]


    useEffect(() => {
        loadPersonas()
    }, [])


    return(
        <Box sx={{flexGrow: 1, flex: 0, padding: 0}}>
            <Container>
                <>
                    <h1> Personas</h1>
                    <Box>
                        <div style={{height: '100%' }}>
                            <DataGrid
                                style={{backgroundColor: '#8ab8ac', color: 'black'}}
                                localeText={esES.components.MuiDataGrid.defaultProps.localeText}
                                autoHeight
                                rows={personas}
                                columns={columnas}
                                pageSize={5}
                                rowsPerPageOptions={[5]}
                                minHeight={750}
                                initialState={{
                                    sorting:{
                                        sortModel: [{field: 'AFechaInicio', sort: 'asc'}],
                                    },
                                }}
                                rowMouseEnter
                                onRowClick={handleRowClick}
                            />
                        </div>
                    </Box>
                </>
                <Toolbar>
                    <Button
                        variant='contained'
                        style={{marginRight: '.5rem',marginLeft: '.5rem', backgroundColor: 'green', color: 'white'}}
                        onClick={() => navigate('/personas/new')}
                        startIcon={<AddCircleOutlineOutlinedIcon />}
                    >
                        Agregar
                    </Button>
                    <Button
                        variant='contained'
                        style={{marginRight: '.5rem',marginLeft: '.5rem', backgroundColor: 'green', color: 'white'}}
                        //onClick={() => navigate('/acreditaciones/new')}
                        startIcon={<AddCircleOutlineOutlinedIcon />}
                    >
                        Editar
                    </Button>
                </Toolbar>
            </Container>
        </Box>
    )
}